<template>
  <div class="unit-wrapper">
    <div class="unit-item" v-for="(item,index) in showRegionStatistics" :key="index">
      <div v-bind:class="{'item-index':true, 'bg-color1': index===0, 'bg-color2': index===1,'bg-color3':index===2 }">{{index+1}}</div>
      <div class="item-content">
          <div class="text">
            <span>{{item.name}}</span>
            <span class="num">{{item.count}}</span>
          </div>
          <div v-bind:style="{width:item.width}" v-bind:class="{'progrecess':true,'bg-color1': index===0, 'bg-color2': index===1,'bg-color3':index===2,'bg-color':index>2 }">
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InUnit',
  props: {
    title: String,
    regionStatistics: Array
  },
  data () {
    return {
      option: {},
      list: [1, 2, 3, 4, 5, 6, 7, 8, 9]
    }
  },

  components: {
  },
  methods: {

  },

  computed: {
    showRegionStatistics () {
      if (this.regionStatistics && this.regionStatistics.length > 0) {
        if (this.regionStatistics.length > 3) {
          return this.regionStatistics.slice(0, 3)
        }
        return this.regionStatistics
      } else {
        return []
      }
    }
  },

  mounted () {
    console.log(this.regionStatistics)
  }
}
</script>

