<template>
  <div class="main">
    <dv-scroll-board class="scroll-board-view"  :config="config" />
  </div>
</template>

<script>
  import { mapState } from 'vuex'
export default {
  name: 'RealTimeAccess',
  props: {
    realTimeOrgJoinInfo: Array
  },
  data () {
    return {
      config: {
        header: ['<span class="scroll-view-header-col">餐饮单位名称</span>',
          '<span class="scroll-view-header-col">安装数量</span>',
          '<span class="scroll-view-header-col">在线数量</span>',
          '<span class="scroll-view-header-col">接入时间</span>'],
        data: [],
        align: ['left'],
        rowNum: 5,
        columnWidth: [450, 150, 150, 150],
        headerBGC: 'rgba(66,159,227,0.12)',
        headerHeight: 0.229 * this.htmlFontSize,
        oddRowBGC: 'rgba(66,159,227,0.12)',
        evenRowBGC: 'rgba(66,159,227,0.12)'
      }
    }
  },
  computed: {
    realTimeOrgJoinInfoLen () {
      return this.realTimeOrgJoinInfo.length
    },
    ...mapState(['htmlFontSize'])
  },

  methods: {
    _fillData () {
      let data = []
      this.realTimeOrgJoinInfo.map(item => {
        data.push(
          [`<span class="scroll-view-content-col">${item.orgName || ''}</span>`,
            `<span class="scroll-view-content-col">${item.total || '0'}</span>`,
            `<span class="scroll-view-content-col">${item.onlineTotal || '0'}</span>`,
            `<span class="scroll-view-content-col">${item.createTime || ''}</span>`]
        )
      })
      this.config = {
        header: ['<span class="scroll-view-header-col">餐饮单位名称</span>',
          '<span class="scroll-view-header-col">安装数量</span>',
          '<span class="scroll-view-header-col">在线数量</span>',
          '<span class="scroll-view-header-col">接入时间</span>'],
        data: data,
        align: ['left'],
        rowNum: 5,
        columnWidth: [450, 150, 150, 280],
        headerBGC: 'rgba(66,159,227,0.12)',
        headerHeight: 0.229 * this.htmlFontSize,
        oddRowBGC: 'rgba(66,159,227,0.12)',
        evenRowBGC: 'rgba(66,159,227,0.12)'
      }
    }
  },
  mounted () {
    if (this.realTimeOrgJoinInfo && this.realTimeOrgJoinInfo.length > 0) {
      this._fillData()
    }
  },

  watch: {
    realTimeOrgJoinInfoLen (newV, oldV) {
      if (newV) {
        this._fillData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.main{
  padding: 0 15px;
}
.scroll-board-view {
  width: 100%;
  height: 460px;
}
</style>
