<template>
  <div class="main">
    <dv-scroll-board class="scroll-board-view"  :config="config" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'cameraTypeList',
  props: {
    realTimeOrgJoinInfo: Array,
    shcy: Object,
    zgst: Object,
    xxst: Object,
    jtjc: Object,
    ygcj: Object
  },
  data () {
    return {
      config: {
        header: ['<span class="scroll-view-header-col">餐饮类型</span>',
          '<span class="scroll-view-header-col">摄像头数</span>',
          '<span class="scroll-view-header-col">近一周新增</span>',
          '<span class="scroll-view-header-col">同比</span>',
          '<span class="scroll-view-header-col">在线率</span>'],
        data: [],
        align: ['left'],
        rowNum: 5,
        columnWidth: [160, 150, 160, 150, 150],
        headerBGC: 'rgba(66,159,227,0.12)',
        headerHeight: 0.229 * this.htmlFontSize,
        oddRowBGC: 'rgba(66,159,227,0.12)',
        evenRowBGC: 'rgba(66,159,227,0.12)'
      },
      list: []
    }
  },
  computed: {
    realTimeOrgJoinInfoLen () {
      return this.realTimeOrgJoinInfo.length
    },
    ...mapState(['htmlFontSize'])
  },

  methods: {
    initData () {
      if (this.shcy) {
        this.list.push({
          name: '社会餐饮',
          ...this.shcy
        })
      } else {
        this.list.push(this._generateDefault('社会餐饮'))
      }
      if (this.xxst) {
        this.list.push({
          name: '学校食堂',
          ...this.xxst
        })
      } else {
        this.list.push(this._generateDefault('学校食堂'))
      }
      if (this.jtjc) {
        this.list.push({
          name: '集体聚餐',
          ...this.jtjc
        })
      } else {
        this.list.push(this._generateDefault('集体聚餐'))
      }
      if (this.zgst) {
        this.list.push({
          name: '职工食堂',
          ...this.zgst
        })
      } else {
        this.list.push(this._generateDefault('职工食堂'))
      }
      if (this.ygcj) {
        this.list.push({
          name: '阳光车间',
          ...this.ygcj
        })
      } else {
        this.list.push(this._generateDefault('阳光车间'))
      }
    },
    _getIncrRatioStatus (incrRatio) {
      if (!incrRatio) return ''
      let ratio = Number(incrRatio)
      if (ratio === 0) {
        return ''
      } else if (ratio > 0) {
        return '<span class="status-red">↑</span>'
      } else {
        return '<span class="status-green">↓</span>'
      }
    },

    _generateDefault (name) {
      return {
        total: 0,
        currentWeekTotal: 0,
        incrRatio: 0,
        onlineRatio: 0,
        name: name
      }
    }
  },

  created () {
    this.initData()
  },
  components: {
  },
  watch: {
    list (newV, oldV) {
      if (newV) {
        let data = []
        this.list.map(item => {
          data.push(
            [`<span class="scroll-view-content-col">${item.name || ''}</span>`,
              `<span class="scroll-view-content-col" style="color: #ffb017;">${item.total || '0'}</span>`,
              `<span class="scroll-view-content-col">${item.currentWeekTotal || '0'}</span>`,
              `<span class="scroll-view-content-col">${item.incrRatio || ''}%</span>${this._getIncrRatioStatus(item.incrRatio)}`,
              `<span class="scroll-view-content-col">${item.onlineRatio || ''}</span>`]
          )
        })
        this.config = {
          header: ['<span class="scroll-view-header-col">餐饮类型</span>',
            '<span class="scroll-view-header-col">摄像头数</span>',
            '<span class="scroll-view-header-col">近一周新增</span>',
            '<span class="scroll-view-header-col">同比</span>',
            '<span class="scroll-view-header-col">在线率</span>'],
          data: data,
          align: ['left'],
          rowNum: 5,
          columnWidth: [160, 150, 160, 150, 150],
          headerBGC: 'rgba(66,159,227,0.12)',
          headerHeight: 0.229 * this.htmlFontSize,
          oddRowBGC: 'rgba(66,159,227,0.12)',
          evenRowBGC: 'rgba(66,159,227,0.12)'
        }
      }
    },
    shcy (newV) {
      this.list.splice(0, 1, {
        name: '社会餐饮',
        ...newV
      })
    },
    xxst (newV) {
      this.list.splice(1, 1, {
        name: '学校食堂',
        ...newV
      })
    },
    jtjc (newV) {
      this.list.splice(2, 1, {
        name: '集体聚餐',
        ...newV
      })
    },
    zgst (newV) {
      this.list.splice(3, 1, {
        name: '职工食堂',
        ...newV
      })
    },
    ygcj (newV) {
      this.list.splice(4, 1, {
        name: '阳光车间',
        ...newV
      })
    }
  }
}
</script>

<style lang="less" scoped>
.main{
  box-sizing: border-box;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items:center;
  align-content: center;
  justify-items: center;
  justify-items: center;
  text-align: center;
  padding: 0 40px;
  margin-top: 45px;
}
  .scroll-board-view {
    width: 100%;
    height: 500px;
  }
</style>
