import Vue from 'vue'
import App from './App.vue'
import store from './store'
import flexible from './flexible'

import './assets/common.less'

import dataV from '@jiaminghi/data-view'

Vue.config.productionTip = false

Vue.use(dataV)
flexible(16 / 9)
new Vue({
  store,
  render: h => h(App)
}).$mount('#app')

