<template>
  <div class="center-cateiner">
    <div class="block-map">
      <div id="water-level-chart" ref="chartRef"></div>
      <div class="top">
        <div class="unit">
          <div class="name">河南省</div>
          <div class="inTolal">共接入单位</div>
        </div>
        <div class="detail">
          <div class="data_show_box">
            <div
              class="data_cage"
              v-for="(item,index) in totalJoinOrg.all.toString()"
              :key="index"
            >{{item}}</div>
          </div>
          <span class="unit" style="margin-left:15px">家</span>
        </div>
        <div class="yesterday">
        <span>
          昨日数量为
          <span class="number">{{totalJoinOrg.previousTotal}}</span> , 新增
          <span class="number">{{totalJoinOrg.todayTotal}}</span>家
        </span>
        </div>
        <vue-seamless-scroll :data="realTimeOrgJoinInfo" class="roll" :class-option="classOption">
          <div class="item" v-for="(item,index) in realTimeOrgJoinInfo" :key="index">
            <div>{{item.orgName}}</div>
            <div>{{item.createTime}}接入</div>
          </div>
        </vue-seamless-scroll>
      </div>
      <div class="bottom">
        <div class="time">{{time}}</div>
        <div class="date">北京时间：{{date}}</div>
      </div>
    </div>
    <div class="block-group">
      <block title="河南省各类餐饮单位占比统计" height="1.563rem" class="dining-type-block">
        <unitType :companyRatio="companyRatio"/>
      </block>
      <block title="实时单位接入统计" height="1.563rem" class="real-time-block">
        <realTimeAccess :realTimeOrgJoinInfo="realTimeOrgJoinInfo"/>
      </block>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/china'
import { currentDate, currentDay } from '../../util/date'
import { getHenanMap } from '../../service/service'
import vueSeamlessScroll from 'vue-seamless-scroll'
import Block from '../../component/block'
import realTimeAccess from '../custom/right/unitType/realTimeAccess'
import unitType from '../custom/right/unitType/unitType'
import { mapState } from 'vuex'

let flagRecord = []
let data = [
  { name: '郑州市', value: 57 },
  { name: '开封市', value: 47 },
  { name: '洛阳市', value: 3 },
  { name: '平顶山市', value: 3 },
  { name: '安阳市', value: 22 },
  { name: '鹤壁市', value: 6 },
  { name: '新乡市', value: 17 },
  { name: '焦作市', value: 34 },
  { name: '濮阳市', value: 33 },
  { name: '许昌市', value: 5 },
  { name: '漯河市', value: 5 },
  { name: '三门峡市', value: 55 },
  { name: '南阳市', value: 23 },
  { name: '商丘市', value: 4 }
]
let geoCoordMap = {
  郑州市: [113.64964385, 34.7566100641],
  开封市: [114.351642118, 34.8018541758],
  洛阳市: [112.447524769, 34.6573678177],
  平顶山市: [113.300848978, 33.7453014565],
  安阳市: [114.351806508, 36.1102667222],
  鹤壁市: [114.297769838, 35.7554258742],
  新乡市: [113.912690161, 35.3072575577],
  焦作市: [113.211835885, 35.234607555],
  濮阳市: [115.026627441, 35.7532978882],
  许昌市: [113.83531246, 34.0267395887],
  漯河市: [114.0460614, 33.5762786885],
  三门峡市: [111.181262093, 34.7833199411],
  南阳市: [112.542841901, 33.0114195691],
  商丘市: [115.641885688, 34.4385886402],
  信阳市: [114.085490993, 32.1285823075],
  周口市: [114.654101942, 33.6237408181]
}

let convertData = function (data) {
  var temp = []
  for (var i = 0; i < data.length; i++) {
    var geoCoord = geoCoordMap[data[i].name]
    if (geoCoord) {
      temp.push({
        name: data[i].name,
        value: geoCoord.concat(data[i].value, data[i].level)
      })
    }
  }
  return temp
}
const seriesdata = function (fontSize) {
  return [
    // 左边地图
    {
      type: 'map',
      map: 'henan',
      aspectScale: 1,
      roam: false,
      left: '30%',
      top: '15%',
      itemStyle: {
        normal: {
          areaColor: 'rgba(0,234,255,0.2)',
          borderColor: '#111',
          color: '#ffffff',
          borderWidth: 0.003 * fontSize,
          label: {
            show: true,
            color: '#ffffff',
            fontSize: 0.088 * fontSize,
            formatter: function (param) {
              if (flagRecord) {
                if (param.name === flagRecord[0]) {
                  return ['{a|' + param.name + '}'].join('\n')
                } else if (param.name === flagRecord[1]) {
                  return ['{b|' + param.name + '}'].join('\n')
                } else if (param.name === flagRecord[2]) {
                  return ['{c|' + param.name + '}'].join('\n')
                }
              }
              return param.name
            },
            rich: {
              a: {
                padding: [0, 0.026 * fontSize, 0.026 * fontSize, 0.026 * fontSize],
                color: '#29fec5',
                fontSize: 0.088 * fontSize,
                fontWeight: 400,
                backgroundColor: 'auto'
              },
              b: {
                padding: [0, 0.026 * fontSize, 0.026 * fontSize, 0.026 * fontSize],
                color: '#f3a956',
                fontSize: 0.088 * fontSize,
                fontWeight: 400,
                backgroundColor: 'auto'
              },
              c: {
                padding: [0.026 * fontSize, 0.026 * fontSize, 0, 0.026 * fontSize],
                color: ' #eac14d',
                fontSize: 0.088 * fontSize,
                fontWeight: 400,
                backgroundColor: 'auto'
              }
            }
          },
          emphasis: {
            label: {
              show: true
            }
          }
        }
      },
      data: data
    }
  ]
}
const option = function (fontSize) {
  return {
    legend: {
      show: false
    },
    // geo: {
    //   map: 'china',
    //   center: [114.4, 23.1],
    //   zoom: 1,
    //   label: {
    //     emphasis: {
    //       show: false
    //     }
    //   },
    //   roam: false,
    //   itemStyle: {
    //     shadowColor: 'rgba(0, 0, 0, 0.5)',
    //     shadowBlur: 10,
    //     borderColor: 'rgba(0,234,255,0.01)',
    //     normal: {
    //       areaColor: 'rgba(0,234,255,0.01)',
    //       borderColor: '#111'
    //     },
    //     emphasis: {
    //       areaColor: 'rgba(0,234,255,0.01)',
    //       borderColor: '#111'
    //     }
    //   }
    // },
    series: seriesdata(fontSize)
  }
}

export default {
  name: 'CenterContainer',
  props: {
    totalJoinOrg: Object,
    realTimeOrgJoinInfo: Array,
    regionStatistics: Array,
    companyRatio: Object
  },
  data () {
    return {
      date: '',
      time: '',
      henanMapData: {}
    }
  },
  computed: {
    classOption () {
      return {
        step: 1,
        singleHeight: parseInt(0.102 * this.htmlFontSize) * 2 + 0.102 * this.htmlFontSize,
        waitTime: 1000
      }
    },
    ...mapState(['htmlFontSize'])
  },
  mounted () {
    this.initChart()
    this.timer = setInterval(this.initTimer, 1000)
  },
  components: {
    Block,
    vueSeamlessScroll,
    realTimeAccess,
    unitType
  },
  created () {},
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.getHenanMapData()
      this.date = currentDay()
    },
    initTimer () {
      this.time = currentDate()
    },
    reSetMap () {
      if (this.henanMapData) {
        this.chart.dispose()
        flagRecord = [
          this.regionStatistics[0].name,
          this.regionStatistics[1].name,
          this.regionStatistics[2].name
        ]
        this.chart = echarts.init(this.$refs.chartRef)
        this.chart.setOption(option(this.htmlFontSize), true)

        // echarts.registerMap('henan', this.henanMapData)
      }
    },
    getHenanMapData () {
      const response = getHenanMap().then(res => {
        echarts.registerMap('henan', res.data)
        this.henanMapData = res.data
        this.chart.setOption(option(this.htmlFontSize), true)
      })
    }
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  },
  watch: {
    regionStatistics: {
      handler (newValue, oldValue) {
        this.reSetMap()
      },
      deep: true
    },
    htmlFontSize () {
      if (this.chart) {
        this.chart.setOption(option(this.htmlFontSize), false, true)
        this.chart.resize()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.center-cateiner {
  height: 100%;
  width: 1865px;
  overflow: hidden;
  position: relative;
  margin-left: 22px;
  .block-map {
    width: 1865px;
    height: 1200px;
    background-color: rgba(0, 0, 0, 0.1);
    position: relative;
  }
  .block-group {
    display: flex;
    flex-direction: row;
    margin-top: 35px;
    .dining-type-block {
      width: 697px;
    }
    .real-time-block {
      width: 1143px;
      margin-left: 23px;
    }
  }
  #water-level-chart {
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 100%;
  }

  .top {
    position: absolute;
    left: 75px;
    top: 75px;
    z-index: 9999;
    .unit {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-content: center;
      justify-content: flex-start;
      .name {
        font-family: SourceHanSansCN-Regular;
        font-size: 80px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #73edfc;
      }
      .inTolal {
        flex: 1;
        font-family: SourceHanSansCN-Regular;
        font-size: 42px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 5px;
        color: #f7e0b9;
        align-self: flex-end;
        margin-left: 58px;
      }
    }
    .detail {
      margin-top: 47px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      .unit {
        width: 43px;
        height: 44px;
        font-family: SourceHanSansCN-Regular;
        font-size: 48px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 48px;
        letter-spacing: 1px;
        color: #f7e0b9;
        text-align: left;
        margin-top: 100px;
      }
      .data_show_box {
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: flex-start;
        justify-items: center;
        height: 146px;
        .data_cage {
          width: 96px;
          height: 146px;
          background-image: linear-gradient(
            0deg,
            #9cecfb 0%,
            #189ba9 51%,
            #65c7f7 100%
          );
          border: solid 1px #65ffff;
          margin-left: 20px;
          text-align: center;
          font-family: SourceHanSansCN-Regular;
          font-size: 95px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 146px;
          letter-spacing: 2px;
          color: #ffffff;
        }
      }
    }
    .yesterday {
      width: 100%;
      height: 50px;
      font-family: SourceHanSansCN-Regular;
      font-size: 48px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 24px;
      letter-spacing: 1px;
      color: #f7e0b9;
      margin-top: 42px;
      .number {
        font-family: SourceHanSansCN-Regular;
        font-size: 48px;
        font-weight: normal;
        letter-spacing: 4px;
        color: #ffb017;
      }
    }
    .roll {
      width: 100%;
      height: 234px;
      overflow: hidden;
      margin: 0;
      padding: 0;
      .item {
        width: 100%;
        font-family: SourceHanSansCN-Regular;
        font-size: 32px;
        line-height: 39px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0;
        margin: 39px 0 0 0;
        padding: 0;
        color: #43ebff;
        .item div {
          height: 39px;
        }
      }
    }
  }
  .bottom {
    width: 650px;
    position: absolute;
    left: 90px;
    bottom: 70px;
    z-index: 9999;
    .time {
      width: 100%;
      font-family: Helvetica;
      font-size: 68px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 1;
      letter-spacing: 8px;
      color: #0bdef3;
    }
    .date {
      width: 100%;
      font-family: SourceHanSansCN-Regular;
      font-size: 30px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      line-height: 1;
      color: #0bdef3;
      margin-top: 20px;
    }
  }
}
</style>
