<template>
  <div class="chart-box" ref="chartRef"></div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'
const dataIPS = []
const dataIPS2 = []
const dataIPS3 = []

const options = function (fontSize) {
  return {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross',
        label: {
          backgroundColor: '#6a7985',
          fontSize: 0.062 * fontSize
        },
        textStyle: {
          fontSize: 0.062 * fontSize
        }
      }
    },
    legend: {
      data: ['未戴白色厨帽', '未戴白色口罩', '未穿白色厨师服'],
      itemGap: 0.08 * fontSize,
      textStyle: {
        color: '#e9ebee',
        fontSize: 0.072 * fontSize
      },
      right: '5%',
      padding: [0.078 * fontSize, 0.026 * fontSize]
    },
    color: ['#0080ff', '#4cd5ce', '#bf4696'],
    toolbox: {
      // feature: {
      //     saveAsImage: {}
      // }
    },
    grid: {
      top: 0.26 * fontSize, // 距上边距
      left: '10%',
      bottom: '12%',
      borderWidth: 0
    },

    xAxis: [
      {
        type: 'category',
        boundaryGap: true,
        data: ['04', '08', '12', '16', '20', '24'],
        splitLine: {
          show: false
        },
        axisLabel: {
          show: true,
          textStyle: {
            color: '#fff',
            align: 'center',
            fontSize: 0.062 * fontSize
          }
        }

      }
    ],
    yAxis: [{
      type: 'value',
      axisLine: {
        onZero: false,
        lineStyle: {
          color: '#0a2b52',
          width: 1 // 这里是为了突出显示加上的
        },
        textStyle: {
          color: '#fff',
          fontSize: 0.062 * fontSize
        }

      },

      axisLabel: {
        formatter: function (val) {
          return val + ''
        },
        show: true,
        textStyle: {
          color: '#fff',
          fontSize: 0.062 * fontSize
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#00e4ff',
          opacity: 0.2
        }

      }
    }],
    series: [
      {
        name: '未戴白色厨帽',
        type: 'line',
        smooth: true,
        symbol: 'none', // 去掉折线点
        itemStyle: {
          normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(176,242,232,0.5)' // 0% 处的颜色
            }, {
              offset: 0.5,
              color: 'rgba(176,242,232,0.5)' // 100% 处的颜色
            }, {
              offset: 1,
              color: 'rgba(56,226,200,0.5)' // 100% 处的颜色
            }]), // 背景渐变色
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'solid',
              color: '#18e4c5'
            }
          },
          emphasis: {
            color: '#18e4c5',
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'dotted',
              color: '#18e4c5' // 折线的颜色
            }
          }
        }, // 线条样式
        symbolSize: 5, // 折线点的大小
        areaStyle: {
          normal: {}
        },
        data: dataIPS
      },

      {
        name: '未戴白色口罩',
        type: 'line',
        smooth: true,
        symbol: 'none', // 去掉折线点
        itemStyle: {
          normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba( 100, 202, 250, 0.5)' // 0% 处的颜色
            }, {
              offset: 0.5,
              color: 'rgba( 100, 202, 250, 0.5)' // 100% 处的颜色
            }, {
              offset: 1,
              color: 'rgba(0, 120, 215, 0.5)' // 100% 处的颜色
            }]), // 背景渐变色
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'solid',
              color: '#0078D7'
            }
          },
          emphasis: {
            color: '#02675f',
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'dotted',
              color: '#02675f' // 折线的颜色
            }
          }
        }, // 线条样式
        symbolSize: 5, // 折线点的大小
        areaStyle: {
          normal: {}
        },
        data: dataIPS2
      },
      {
        name: '未穿白色厨师服',
        type: 'line',
        smooth: true,
        symbol: 'none', // 去掉折线点
        itemStyle: {
          normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(189,180,194,0.5)' // 0% 处的颜色
            }, {
              offset: 0.5,
              color: '#7b5e8a' // 100% 处的颜色
            }, {
              offset: 1,
              color: 'rgba(123,94,138,0.5)' // 100% 处的颜色
            }]), // 背景渐变色
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'solid',
              color: 'rgba(244,142,186,0.5)'
            }
          },
          emphasis: {
            color: '#f48eba',
            lineStyle: { // 系列级个性化折线样式
              width: 1,
              type: 'dotted',
              color: '#f48eba' // 折线的颜色
            }
          }
        }, // 线条样式
        symbolSize: 5, // 折线点的大小
        areaStyle: {
          normal: {}
        },
        data: dataIPS3
      }

    ]
  }
}
export default {
  name: 'cook',
  props: {
    chefMaps: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.setChartData()
  },
  computed: {
    ...mapState(['htmlFontSize'])
  },
  methods: {
    setChartData (lazyUpdate) {
      if (!this.chart) this.chart = echarts.init(this.$refs.chartRef)
      let option = options(this.htmlFontSize)
      option.series[0].data = this.chefMaps.hatMap || []
      option.series[1].data = this.chefMaps.maskMap || []
      option.series[2].data = this.chefMaps.clothesMap || []
      this.chart.setOption(option, false, lazyUpdate)
    }
  },
  watch: {
    chefMaps () {
      this.setChartData()
    },
    htmlFontSize () {
      this.setChartData(true)
      this.chart.resize()
    }
  }

}
</script>

<style lang="less">
.chart-box{
  width: 100%;
  height: 400px;
}
</style>
