<template>
  <div class="unitType">
    <div class="main" ref="chartRef"></div>
    <div class="sub-title">各类餐饮企业上线舌尖安全网占比统计</div>
    <div class="company-ratio-list">
      <div class="company-ratio" v-for="(ratio,idx) in companyRatioList" :key="idx">
        <div class="company-ratio-value" :style="{color: ratio.color}">{{ratio.value}}%</div>
        <div class="company-name">{{ratio.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'
const option = function (fontSize) {
  return {
    legend: {
      orient: 'vertical',
      itemHeight: 0.07 * fontSize,
      right: 0.104 * fontSize,
      top: 0.052 * fontSize,
      bottom: 0.052 * fontSize,
      itemGap: 0.065 * fontSize,
      textStyle: {
        color: '#e9ebee',
        fontSize: 0.072 * fontSize
      },
      data: ['学校食堂', '社会餐饮', '农村聚餐', '阳光车间', '职工食堂']
    },
    series: [
      {
        name: '访问来源',
        type: 'pie',
        radius: 0.28 * fontSize,
        center: ['30%', '50%'],
        data: [
          {
            value: 234,
            name: '学校食堂',
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#18e4c5'
              }
            }
          },
          {
            value: 234,
            name: '社会餐饮',
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#3278e1'
              }
            }
          },
          {
            value: 234,
            name: '农村聚餐',
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#0dd5ed'
              }
            }
          },
          {
            value: 234,
            name: '阳光车间',
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#f3a956 '
              }
            }
          },
          {
            value: 234,
            name: '职工食堂',
            label: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#dc739d'
              }
            }
          }
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 0.026 * fontSize,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        },
        labelLine: {
          show: false,
          normal: {
            lineStyle: {
              width: 0.005 * fontSize
            },
            length: 0.02 * fontSize,
            length2: 0.02 * fontSize
          }
        },
        label: {
          // 饼图图形上的文本标签
          normal: {
            show: true,
            position: 'outside', // 标签的位置
            textStyle: {
              fontWeight: 400,
              fontSize: 0.057 * fontSize // 文字的字体大小
            },
            formatter: '{c}%'
          }
        }
      }
    ]
  }
}

export default {
  name: 'unitType',
  props: {
    companyRatio: Object
  },
  data () {
    return {
      companyRatioList: [],
      // companyTypeColors: {
      //   "1": "#3278e1",
      //   "2": "#18e4c5",
      //   "3": "#dc739d",
      //   "4": "#0dd5ed",
      //   "5": "#f3a956"
      // }
      companyTypeColors: {
        '社会餐饮': '#3278e1',
        '学校食堂': '#18e4c5',
        '职工食堂': '#dc739d',
        '农村聚餐': '#0dd5ed',
        '阳光车间': '#f3a956'
      }
    }
  },
  mounted () {
    this.setChartData()
  },
  computed: {
    ...mapState(['htmlFontSize'])
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option(this.htmlFontSize))
    },
    setChartData (lazyUpdate) {
      if (!this.chart) this.chart = echarts.init(this.$refs.chartRef)
      const { companyTotal, companyRatio } = this.companyRatio

      let options = option(this.htmlFontSize)
      if (companyRatio) {
        let newData = []
        const objChangeMap = (obj) => {
          let map = new Map()
          for (let key in obj) {
            map.set(key, obj[key])
          }
          return map
        }

        const resultMap = objChangeMap(companyRatio)
        if (resultMap && resultMap.size > 0) {
          for (let [k, v] of resultMap) {
            newData.push({
              value: v,
              name: k,
              color: this.companyTypeColors[k],
              itemStyle: {
                normal: {
                  color: this.companyTypeColors[k]
                }
              }
            })
          }
        }
        this.companyRatioList = newData
        options.series[0].data = newData
      }

      // if (companyTotal && companyTotal instanceof Array) {
      //   let newData = []
      //   let ratioList = []
      //   let total = 0
      //   companyTotal.forEach(item => {
      //     newData.push({
      //       value: item.count,
      //       name: item.controlUnitName,
      //       itemStyle: {
      //         normal: {
      //           color: this.companyTypeColors[String(item.controlUnitId)]
      //         }
      //       }
      //     })
      //     total += item.count
      //   })

      //   companyTotal.forEach(item => {
      //     ratioList.push({
      //       name: item.controlUnitName,
      //       value: (item.count / total).toFixed(2) * 100,
      //       color: this.companyTypeColors[String(item.controlUnitId)]
      //     })
      //   })
      //   this.companyRatioList = ratioList
      //   options.series[0].data = newData
      // }
      this.chart.setOption(options, false, lazyUpdate)
    }
  },
  watch: {
    companyRatio: {
      handler (newValue, oldValue) {
        this.setChartData()
      },
      deep: true
    },
    htmlFontSize () {
      this.setChartData(true)
      this.chart.resize()
    }
  }
}
</script>

<style lang="less" scoped>
//  .main {
//   width: 100%;
//   height: 601px;
//   margin-top: 54px;
//   display: flex;
//   flex-direction: row;
// }
.unitType {
  .main {
    width: 100%;
    height: 300px;
  }
  .sub-title {
    font-size: 28px;
    color: white;
    text-align: center;
    margin-top: 20px;
  }
  .company-ratio-list {
    background: linear-gradient(
          rgba(58, 112, 176, 0.2),
          rgba(58, 112, 176, 0.2)
        )
        left top,
      linear-gradient(#51bef9, #51bef9) left top,
      linear-gradient(#51bef9, #51bef9) left top,
      linear-gradient(#51bef9, #51bef9) right top,
      linear-gradient(#51bef9, #51bef9) right top,
      linear-gradient(#51bef9, #51bef9) left bottom,
      linear-gradient(#51bef9, #51bef9) left bottom,
      linear-gradient(#51bef9, #51bef9) right bottom,
      linear-gradient(#51bef9, #51bef9) right bottom;
    background-repeat: no-repeat;
    background-size: 100% 100%, 3px 20px, 20px 3px, 3px 20px, 20px 3px, 3px 20px,
      20px 3px, 3px 20px, 20px 3px;
    display: flex;
    flex-direction: row;
    margin: 10px 17px;
    .company-ratio {
      text-align: center;
      padding: 15px 0;
      flex: 1;
      .company-ratio-value {
        font-size: 36px;
        line-height: 1;
      }
      .company-name {
        font-size: 26px;
        line-height: 1;
        margin-top: 15px;
        letter-spacing: 0;
      }
    }
  }
}
</style>
