<template>
  <div class="main">
    <div class="content">
      <div class="title">
        <div class="text">在线率</div>
      </div>
      <div class="detail">
        <div class="chart-box" ref="chartRef"></div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'

const option = function (fontSize) {
  return {
    series: {
      type: 'pie',
      clockWise: false,
      radius: [0.27 * fontSize, 0.28 * fontSize],
      hoverAnimation: false,
      silent: true,
      center: ['50%', '50%'],
      data: [{
        value: 75,
        name: 'invisible',
        itemStyle: {
          normal: {
            color: '#7bf5fe',
            borderWidth: 1,
            borderColor: '#073A66'

          }
        },
        label: {
          show: false
        },
        labelLine: {
          show: false
        }
      }, {
        value: 25,
        label: {
          normal: {
            rich: {
              a: {
                color: '#7bf5fe',
                align: 'center',
                fontSize: 0.16 * fontSize,
                fontWeight: 'bold',
                fontFamily: 'SourceHanSansCN-Medium'
              },
              b: {
                color: '#7bf5fe',
                align: 'center',
                fontSize: 0.08 * fontSize
              },
              c: {
                fontSize: 0.08 * fontSize,
                fontFamily: 'SourceHanSansCN-Medium',
                fontWeight: 'bold'
              }
            },
            formatter: function (params) {
              return '{a|' + params.value + '}' + ' {c|%}' + '\n\n{b|在线}'
            },
            position: 'center',
            show: true,
            textStyle: {
              fontSize: 0.031 * fontSize,
              fontWeight: 'normal',
              color: '#fff'
            }
          }
        },

        itemStyle: {
          normal: {
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
            color: '#7bf5fe',
            shadowColor: '#7bf5fe',
            borderWidth: 0.031 * fontSize,
            borderColor: '#82ffff',
            shadowBlur: 5
          }
        }
      }]
    }
  }
}
export default {
  name: 'OnlineProbability',
  props: {
    onlineRatio: String
  },
  data () {
    return {
      option: {}
    }
  },
  computed: {
    ...mapState(['htmlFontSize'])
  },
  mounted () {
    this.initChart()
  },

  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option(this.htmlFontSize))
    }
  },
  watch: {
    onlineRatio (newV) {
      let opt = option(this.htmlFontSize)
      const v = parseInt(newV)
      opt.series.data[0].value = 100 - v
      opt.series.data[1].value = v
      this.chart.setOption(opt)
    },
    htmlFontSize () {
      let opt = option(this.htmlFontSize)
      const v = parseInt(this.onlineRatio)
      opt.series.data[0].value = 100 - v
      opt.series.data[1].value = v
      this.chart.setOption(opt, false, true)
      this.chart.resize()
    }
  }
}
</script>

<style lang="less" scoped>
.main {
  margin: 15px;
  .content {
    padding: 70px 50px 0 50px;
    position: relative;
    .title {
      position: absolute;
      display: flex;
      flex-direction: row;
      align-content: flex-start;
      align-items: center;
      justify-content: flex-start;
      .text {
        text-align: left;
        flex: 1;
        font-family: SourceHanSansCN-Medium;
        font-size: 36px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 1;
        color: #00e4ff;
      }
      .img {
        width: 100px;
        display: flex;
        flex-direction: row;
        .item {
          display: block;
          height: 20px;
          width: 5px;
          margin-right: 8px;
          background-color: #00e4ff;
          transform: skewX(-40deg);
        }
      }
    }
    .line {
      display: block;
      width: 686px;
      height: 3px;
      margin-top: 30px;
      background-color: #00e4ff;
      opacity: 0.07;
    }
    .detail {
      width: 100%;
      height: 250px;
      display: flex;
      align-content: center;
      justify-content: center;
      align-items: center;
      text-align: center;
      .chart-box {
        width: 100%;
      }
    }
  }
}
</style>
