import Axios from 'axios'

const baseURL = '/api'
// const baseURL = 'http://127.0.0.1:9090/api'
// const baseURL = 'http://my.rhjc56.com:8082/rhslp/';

const http = Axios.create({
  baseURL
})
const cancelToken = Axios.CancelToken
const source = cancelToken.source()
// 请求拦截器处理
http.interceptors.request.use(req => beforeRequest(req), error => Promise.reject(error))
http.interceptors.response.use(
  response => dataFormat(response),
  error => dealWithRequestError(error)
)
// 状态处理
const dealWithRequestError = error => {
  const status = error && error.response ? error.response.status : ''
  let msgInfo = ''
  switch (status) {
    case 400:
      msgInfo = '请求出错'
      break
    case 401:
      msgInfo = '没有权限'
      break
    case 403:
      msgInfo = '访问被拒绝'
      break
    case 404:
      msgInfo = '请求地址出错'
      break
    case 408:
      msgInfo = '请求超时'
      break
    case 500:
      msgInfo = '服务器内部出错'
      break
    case 501:
      msgInfo = '服务未实现'
      break
    case 502:
      msgInfo = '网关出错'
      break
    case 503:
      msgInfo = '服务不可用'
      break
    case 504:
      msgInfo = '网关超时'
      break
    case 505:
      msgInfo = 'HTTP版本不支持'
      break
    default:
      break
  }
  return {}
}

// 默认配置
const defaultConfig = {
  headers: {
    'Content-Type': 'application/json;charset=UTF-8'
  },
  withCredentials: true,
  timeout: 10000
}

// 请求前
const beforeRequest = config => {
  return config
}

// 数据解析
const dataFormat = response => {
  if (!response) return
  const { code, message } = response.data || {}
  if (code === 401) {
    // Message.warning('请先登录');
    return response.data
  }
  if (code === 502) {
    // Message.warning('登录状态失效，请重新登录');
    return response.data
  }
  // if (code !== 0) Message.warning(message || '未知错误');
  return response.data
}

// 构建请求
const buildRequest = async (method, url, params = {}, config = {}, serialize = true) => {
  config = {
    ...defaultConfig,
    ...config
  }
  if (serialize) {
    params = JSON.stringify(params)
  }
  if (method === 'delete') {
    return http[method](url, {
      params,
      ...config
    })
  }
  return http[method](url, params, config)
}

// 如果是get请求则拼接url地址
const combinUrlStr = (url, data) => {
  const arr = []
  const defaults = {
    t: new Date().getTime()
  }
  if (!data) {
    data = defaults
  } else {
    data = Object.assign(data, defaults)
  }
  for (const key in data) {
    arr.push(`${key}=${encodeURIComponent(data[key])}`)
  }
  return `${url}?${arr.join('&')}`
}

export const get = (url, params, config) => buildRequest('get', combinUrlStr(url, params), params, config)
export const post = (url, params, config) => buildRequest('post', url, params, config)
export const put = (url, params, config) => buildRequest('put', url, params, config)
export const del = (url, params, config) => buildRequest('delete', url, params, config)
export const postForm = (
  url,
  params,
  config = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  }
) => buildRequest('post', url, params, config)
export const all = iterable => Axios.all(iterable)
export const { cancel } = source
export const postFormData = (url, params, config) => {
  config = {
    ...defaultConfig,
    ...config
  }
  return http.post(url, params, config)
}
export const getImg = actionName => baseURL + actionName
