const currentDate = () => {
  const date = new Date()
  const year = date.getFullYear() // 获取当前年份
  const mon = date.getMonth() + 1 // 获取当前月份
  const da = date.getDate() // 获取当前日
  const day = date.getDay() // 获取当前星期几
  const h = date.getHours() // 获取小时
  const m = date.getMinutes() // 获取分钟
  const s = date.getSeconds() // 获取秒
  const d = document.getElementById('Date')
  // return h + ':' + m + ':' + s
  return (h.toString().length === 1 ? '0' + h : h) + ':' + (m.toString().length === 1 ? '0' + m : m) + ':' + (s.toString().length === 1 ? '0' + s : s)
}

const currentDay = () => {
  const date = new Date()
  const year = date.getFullYear() // 获取当前年份
  const mon = date.getMonth() + 1 // 获取当前月份
  const da = date.getDate() // 获取当前日
  return year + '年' + (mon.toString().length === 1 ? '0' + mon : mon) + '月' + (da.toString().length === 1 ? '0' + da : da) + '日'
}

export {
  currentDate, currentDay
}
