<template>
  <div class="chart-box" ref="chartRef"></div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'
const options = function (fontSize) {
  return {
    tooltip: {
      trigger: 'axis'
    },
    grid: {
      top: 0.26 * fontSize, // 距上边距
      left: '10%',
      bottom: '12%',
      borderWidth: 0
    },

    calculable: false,
    legend: {
      data: ['消毒柜', '留样柜', '紫外灯'],
      itemGap: 0.156 * fontSize,
      textStyle: {
        color: '#e9ebee',
        fontSize: 0.072 * fontSize
      },
      right: '5%',
      padding: [0.078 * fontSize, 0.026 * fontSize]
    },
    xAxis: [
      {
        type: 'category',
        boundaryGap: true,
        data: ['04', '08', '12', '16', '20', '24'],
        splitLine: {
          show: false
        },
        axisLabel: {
          show: true,
          textStyle: {
            color: '#fff',
            align: 'center',
            fontSize: 0.062 * fontSize
          }
        }

      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          formatter: '{value} ',
          textStyle: {
            color: '#fff',
            align: 'right',
            fontSize: 0.062 * fontSize
          }
        },
        splitLine: {
          show: true,
          lineStyle: {
            type: 'dashed',
            color: '#00e4ff',
            opacity: 0.2
          }
        }
      }

    ],
    series: [
      {
        name: '消毒柜',
        type: 'custom',
        data: [],
        itemStyle: {
          normal: {
            color: '#18e4c5'
          }
        },
        barMinHeight: 2
      },
      {
        name: '留样柜',
        type: 'custom',
        data: [],
        itemStyle: {
          normal: {
            color: '#3e93f5'
          }
        },
        barMinHeight: 2
      },
      {
        name: '紫外灯',
        type: 'line',
        data: [],
        itemStyle: {
          normal: {
            color: '#ffb017'
          }
        }
      }
    ]
  }
}
export default {
  name: 'sterilizer',
  props: {
    cabinet: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.setChartData()
  },
  computed: {
    ...mapState(['htmlFontSize'])
  },
  methods: {
    _isCenter (values, idx) {
      return !values || values[idx] === undefined || values[idx] === 0
    },
    setChartData (lazyUpdate) {
      if (!this.chart) this.chart = echarts.init(this.$refs.chartRef)
      const self = this
      const { sampleCabinetMap, disinfectionCabinetMap, lampMap } = this.cabinet
      let newSeries = []
      newSeries.push({
        name: '消毒柜',
        type: 'custom',
        data: disinfectionCabinetMap || [],
        itemStyle: {
          normal: {
            color: '#18e4c5'
          }
        },
        renderItem: function (params, api) {
          let coord = api.coord([api.value(0), api.value(1)])
          let size = api.size([1, api.value(1)])
          let width = size[0] * 0.35
          let offset = 0
          if (self._isCenter(sampleCabinetMap, api.value(0))) {
            offset = -width / 2
          } else {
            offset = -size[0] * 0.10 / 2 - width
          }
          return {
            type: 'rect',
            shape: {
              x: coord[0] + offset,
              y: coord[1],
              width: width,
              height: size[1]
            },
            style: api.style()
          }
        }
      })
      newSeries.push({
        name: '留样柜',
        type: 'custom',
        data: sampleCabinetMap || [],
        itemStyle: {
          normal: {
            color: '#3e93f5'
          }
        },
        renderItem: function (params, api) {
          let coord = api.coord([api.value(0), api.value(1)])
          let size = api.size([1, api.value(1)])
          let width = size[0] * 0.35
          let offset = 0
          if (self._isCenter(disinfectionCabinetMap, api.value(0))) {
            offset = -width / 2
          } else {
            offset = size[0] * 0.10 / 2
          }
          return {
            type: 'rect',
            shape: {
              x: coord[0] + offset,
              y: coord[1],
              width: width,
              height: size[1]
            },
            style: api.style()
          }
        }
      })
      newSeries.push({
        name: '紫外灯',
        type: 'line',
        data: lampMap || [],
        itemStyle: {
          normal: {
            color: '#ffb017'
          }
        }
      })
      let opt = options(this.htmlFontSize)
      opt.series = newSeries
      this.chart.setOption(opt, false, lazyUpdate)
    }
  },
  watch: {
    cabinet: {
      handler (newValue, oldValue) {
        this.setChartData()
      },
      deep: true
    },
    htmlFontSize () {
      this.setChartData(true)
      this.chart.resize()
    }
  }

}
</script>

<style lang="less" scoped>
.chart-box{
  width: 100%;
  height: 400px;
}
</style>
